.typing {
    background: #172238;
    width: 800px;
    height: 250px;
    overflow: hidden;
    -webkit-transition: margin .5s linear;
    -o-transition: margin .5s linear;
    transition: margin .5s linear;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.container {
    margin-top: 50px;
    overflow: hidden;
}

.word {
    font-size: 30px;
    float: left;
    color: #FFF;
    margin: 4px 8px 8px 8px;
    font-family: 'Lato', sans-serif;
}

.typed-correctly {
  color: green;
}

.typed-incorrectly {
  color: red;
}


